<template>
    <!---->
  <carousel-3d
    :autoplay="true"
    :autoplayTimeout="3000"
    :perspective="50"
    :display="5"
    :animationSpeed="1000"
    :width="512"
    :height="400"
    controlsVisible
    :controlsHeight="60"
    style="background: none; border: 0px; text-align:center;">
    <slide v-for="(item, i) in slides" :index="i" :key="i">{{item.text}}
        <!---->
      <template slot-scope="obj">
        <img @mouseover="show(obj)" @mouseleave="disshow(obj)" @click="imgClick(obj, item.type)" :src='item.src' alt="" style="height: 321px;width: 514px; border-radius: 19px">
        <h2 v-show="item.isShow">{{item.text}}</h2>
        <h3 v-show="item.isShow">{{item.postscript}}</h3>
      </template>  
    </slide>
    
  </carousel-3d>
</template>


<script>
import { Carousel3d, Slide } from 'vue-carousel-3d'
export default {
    name:'Homebanner',
    components: {
        Carousel3d,
        Slide
    },
    data(){
        return{
            slides:[
                {text:'',type:'me',src:require('../../assets/homebanner/me1.png'),postscript:'',isShow:false},
                {text:'',type:'page',src:require('../../assets/homebanner/page1.png'),postscript:'',isShow:false},
                {text:'',type:'work',src:require('../../assets/homebanner/work1.png'),postscript:'',isShow:false},
                {text:'',type:'skill',src:require('../../assets/homebanner/skill1.png'),postscript:'',isShow:false},
                {text:'',type:'me',src:require('../../assets/homebanner/me1.png'),postscript:'',isShow:false},
                {text:'',type:'page',src:require('../../assets/homebanner/page1.png'),postscript:'',isShow:false},
                {text:'',type:'work',src:require('../../assets/homebanner/work1.png'),postscript:'',isShow:false},
                {text:'',type:'skill',src:require('../../assets/homebanner/skill1.png'),postscript:'',isShow:false},
                ],
        }
    },
    computed:{
        
    },
    methods:{
        imgClick(i,type){
            if(i.leftIndex==-1&i.rightIndex==-1){
                this.$router.push({path:'/profile/partprofile',query: {type:type}})   
            }
        },
        show(i){
            let a = i.index
            if(a<3){
                this.slides[a].isShow = true
            }else{
                this.slides[a].isShow = true
            }

        },
        disshow(i){
            let a = i.index
            if(a<3){
                this.slides[a].isShow = false
            }else{
                this.slides[a].isShow = false
            }

        }
    }
}
</script>
